<template>
	<account activeTab="availability">
		<section>
			<div class="field_title pt-1 mb-1 d-flex align-items-center">
				<BIcon icon="arrow-repeat" class="mr-2" />
				<div class="title font-weight-500">{{ $t("defaultAvailability") }}</div>
			</div>
			<p class="fs-sm text-muted">{{ $t("defaultAvailabilityDesc") }}</p>
			<div class="mt-3">
				<div class="mb-5">
					<b-list-group>
						<AvailableItem v-for="(date, i) in range" :key="i" :date="date" :availability="availability" @click="openAvailabilityModal" dateFormat="dddd" />
					</b-list-group>
				</div>
			</div>
		</section>
		<AvailabilityModal :duration="30" ref="availabilityModal" @submit="availableDateChanged" dateFormat="dddd" noRepeat />
	</account>
</template>

<script>
	import Account from "@/components/Account";
	import AvailableItem from "@/components/AvailableItem";
	import AvailabilityModal from "@/components/modals/AvailabilityModal";

	export default {
		components: { Account, AvailableItem, AvailabilityModal },

		data() {
			return {
				availability: [],
			};
		},

		mounted() {
			this.availability = this.$me.defaultWeekAvailability.map((availabile) => {
				let { rrule, duration } = availabile;

				rrule = rrule.replace(/DTSTART:YYYYMMDDTHHmmssZ/g, "DTSTART:" + this.$moment().utc().startOf("week").startOf("date").locale("en").format("YYYYMMDDTHHmmss") + "Z");

				rrule = rrule.replace(/UNTIL=YYYYMMDDTHHmmssZ/g, "UNTIL=" + this.$moment().utc().endOf("week").startOf("date").locale("en").format("YYYYMMDDTHHmmss") + "Z");

				return { duration, rrule };
			});
		},

		computed: {
			range() {
				let range = [];
				let start = this.$moment().startOf("week").startOf("date");
				let end = this.$moment().endOf("week").startOf("date");
				let current = start.clone();
				while (current <= end) {
					range.push(current.toDate());
					current.add(1, "day");
				}
				return range;
			},
		},

		methods: {
			openAvailabilityModal(available) {
				this.$refs.availabilityModal.show(available);
			},

			availableDateChanged(value) {
				console.log(value);
			},
		},
	};
</script>

<style lang="scss"></style>
