<template>
	<div class="account mt-5 position-relative">
		<b-container>
			<div class="header mt-4">
				<div class="h3 mb-0">{{ $t("accountSettings") }}</div>
			</div>
			<div class="my-4 py-2">
				<b-nav fill class="custom_nav">
					<b-nav-item v-for="tab in tabs" :key="tab.value" :active="isActive(tab)" :to="tab.to">{{ tab.title }}</b-nav-item>
				</b-nav>
			</div>
			<b-row>
				<b-col cols="12" md="9">
					<slot></slot>
				</b-col>
			</b-row>
		</b-container>
	</div>
</template>

<script>
	export default {
		props: {
			activeTab: { type: String, default: "general" },
		},

		data() {
			return {
				tabs: [
					{ title: this.$t("general"), value: "general", isActive: true, to: "/account" },
					{ title: this.$t("calendars"), value: "calendars", isActive: false, to: "/account/calendars" },
					{ title: this.$t("integrations"), value: "integrations", isActive: false, to: "/account/integrations" },
					{ title: this.$t("availability"), value: "availability", isActive: false, to: "/account/availability" },
					{ title: this.$t("contacts"), value: "contacts", isActive: false, to: "/account/contacts" },
				],
			};
		},

		methods: {
			isActive(tab) {
				return tab.value == this.activeTab;
			},
		},
	};
</script>

